import React from 'react'
import Book from './Book'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
export default class BookList extends React.Component {
    constructor(props) {
      super(props);
    }

    render() {
    const books = [
            {
                title: 'One Last Stop', 
                author: 'Casey', 
                img: 'https://images-na.ssl-images-amazon.com/images/I/41uORm5aOQS._AC_SX184_.jpg',
                id:'abc'
            },
            {
                title: 'Haven Point',
                author: 'Virginia',
                img: 'https://images-na.ssl-images-amazon.com/images/I/51n4vZzOsSS._AC_SX184_.jpg',
                id:'adc'
            }
        ]
    
        return( 
        <div>
                {books.map((book) =>{
                    return (
                        <div  key={book.id} className="Book" style={{border:'1px solid black', width:'17.4%', margin:'10px auto'}}>
                                <img src={book.img} alt=""/>
                                <h4>{book.title}</h4>     
                                <p>{book.author}</p>   
                        </div>
                    )
                })}
        </div>

        )
    }
  }